import React from 'react';

import './style/StartView.css';
export default class StartView extends React.Component{
  render(){
    return(
      <div className="start-element">
      <div className="game-title">ゆうせいくん 育成ゲーム (仮)</div>
      <button onClick={()=>{
        this.props.startClick()
       }} className="start-button ">
        start
      </button>
      <div className="game-caution">
        [注意]<br/>
        ☆ 現在作成中につき<br/>
        　一部機能は正しく動作していません<br/>
        ☆ 個人製作品につき品質等保証はしません<br/>
        ☆ 現在実装済み機能<br/>
        　　・走る<br/>
        　　・座らせる<br/>
        　　・アイテム画面表示<br/>
        　　・撫でる<br/>
        ☆ わかりづらい未実装<br/>
        　　・レベルアップ<br/>
        　　・Windowサイズ途中変更<br/>
        　　　(再読み込みしてください)
      </div>
      <div className="credit">
        制作： トキハゲン　　　　　　キャラクター著作： 朝雲
      </div>
    </div>
    )
  }
}

